@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&family=Yusei+Magic&display=swap);
html{scroll-behavior:smooth}*,*::after *::before{box-sizing:border-box;padding:0;margin:0}body{font-family:'Montserrat', sans-serif;padding:0;margin:0}

.hamburger{position:absolute;top:0;left:0;padding:10px;margin:16px;display:inline-block;border:none;background-color:transparent;cursor:pointer;z-index:3;transform:translateX(0);transition:.3s transform ease-in-out}.hamburger:focus{outline:none}.hamburger .hamburger__box{width:40px;height:24px;display:inline-block;position:relative}.hamburger__inner{width:100%;height:4px;position:absolute;background-color:#fff;left:0;top:50%;transition:.3s background-color ease-in-out}.hamburger__inner:after{content:'';width:100%;height:4px;position:absolute;background-color:#fff;left:0;top:10px;transition:.3s transform ease-in-out}.hamburger__inner:before{content:'';width:100%;height:4px;position:absolute;background-color:#fff;left:0;top:-10px;transition:.3s transform ease-in-out}.hamburger_active .hamburger__inner{background-color:transparent}.hamburger_active .hamburger__inner:after{transform:translateY(-10px) rotate(-45deg)}.hamburger_active .hamburger__inner:before{transform:translateY(10px) rotate(45deg)}.hamburger_active{transform:translateX(210px)}

.nav{height:100vh;width:300px;position:absolute;top:0;left:0;transform:translateX(-100%);background-color:#ebc55b;border-right:6px solid #585858;transition:.3s transform ease-in-out;z-index:2}.nav__active{transform:translateX(0px)}.nav__list{margin-top:120px;list-style:none;padding:0}.nav__list--item{margin:24px;text-align:center}.nav__link{display:block;text-decoration:none;color:#fff;padding:8px;text-align:center;background-color:#409489;border:4px solid #616161;border-radius:5px;font-size:24px;transition:.3s background-color ease-in-out}.nav__link:hover{background-color:rgba(133,133,133,0.911);color:#292929}.lastItem{margin-top:120px;background-color:#a59f52}

.gamePage{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center}.logo{background:#D9D9D9;box-shadow:0px 0px 9px rgba(0,0,0,0.25);border-radius:15px;padding:5px 30px;color:#FF0000;font-size:42px;margin:150px 0 64px 0}.logo__color1{color:#797979}.logo__color2{color:#2F2F2F}@media (max-width: 786px){.logo{margin:80px 0 64px 0}}.button{border-radius:50%;box-shadow:0px 8px 9px rgba(0,0,0,0.44);transition:0.1s box-shadow ease-in-out;margin-bottom:32px}.button:active{box-shadow:0px 8px 9px rgba(0,0,0,0.11);transform:translateY(5px) scale(0.98)}.score__box{display:inline-block;margin-top:32px;background:rgba(233,233,233,0.71);border:5px solid #AEAEAE;box-sizing:border-box;box-shadow:0px 6px 4px rgba(0,0,0,0.39);border-radius:20px;width:220px;height:65px;position:relative}.score__box::before{content:'SCORE';font-size:24px;font-weight:normal;color:#646464;position:absolute;top:-35px}.score{padding:8px;color:#575757;float:right;font-size:32px;font-weight:normal;margin:0}


.achievements{width:100%;height:100%;min-height:100vh;display:flex;align-items:center;flex-direction:column;position:relative}.achievements__header{margin-top:64px}.achievements__grid{max-width:1440px;max-height:100%;margin:0 auto;display:grid;grid-template-columns:1fr 1fr 1fr;margin:16px}@media (max-width: 960px){.achievements__grid{grid-template-columns:1fr 1fr}}@media (max-width: 786px){.achievements__grid{grid-template-columns:1fr;margin:0}}.achievement__boxitem{margin:16px 32px;display:flex;flex-direction:column;justify-content:center;align-items:center;max-width:255px}@media (max-width: 786px){.achievement__boxitem{margin:8px}}.achievement__element{text-align:center;border:8px solid rgba(80,80,80,0.829);border-radius:16px;padding:16px;width:150px;min-height:120px}.achievement__name{margin:0;font-size:32px;font-weight:600;text-transform:uppercase;color:#292929}@media (max-width: 786px){.achievement__name{font-size:24px}}.achievement__description{font-size:24px;color:#292929}

.upgrades{min-height:100vh;width:100%;height:100%;display:flex;align-items:center;flex-direction:column}.upgrade{position:relative;color:#fff;font-size:24px;padding:20px 38px;border-radius:16px;transition:.3s background-color ease-in-out}.upgrade:focus{outline:none}.upgrade__on{background-color:#fff}.upgrade__on::before{content:'';position:absolute;height:50px;width:50px;background-color:#000;top:-6px;border-radius:50%;transform:translateX(0);transition:.4s transform ease-in-out;border:2px solid #313131}.upgrade__off{background-color:#000}.upgrade__off::before{content:'';position:absolute;height:50px;width:50px;background-color:#fff;top:-6px;border-radius:50%;transform:translateX(-45px);transition:.3s transform ease-in-out;border:2px solid black}.upgrade__button{width:150px;height:50px;background-color:#d4d4d4;border:4px solid #272727;border-radius:16px;font-size:16px;font-weight:600;cursor:pointer;transition:.2s background-color ease-in-out;margin:0 16px}.upgrade__button:hover{background-color:beige}.upgrade__button:active{transform:scale(0.99)}.information{display:flex;padding:64px 0;text-align:center}.information__box{padding:16px}.information__header{font-size:24px;font-weight:600;margin:0}.upgrade__section{margin-top:16px;text-align:center;display:flex}.upgrade__section__box{padding:32px}@media (max-width: 786px){.upgrade__section{display:flex;flex-direction:column}}.isActive{display:flex;flex-direction:column;justify-content:center;align-items:center}.isActive__text{font-size:16px;padding-right:16px}.isActive__text--bold{font-weight:600}

.page{width:100%;height:100%;background-color:#D9D9D9;box-shadow:inset 0px 1px 5px rgba(0,0,0,0.25)}

.settings{width:100%;height:100vh;display:flex;align-items:center;justify-content:center}.confirmationView{width:100%;height:100vh;position:absolute;top:0;left:0;background-color:#707070;display:flex;flex-direction:column;align-items:center;justify-content:center}.confirmationButton{height:30px;width:90px;font-size:16px;font-weight:600;background-color:#fff;border:3px solid #111;margin:32px}.confirmationText{color:black;font-size:32px}

.error{width:100%;height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center}.error__button{text-decoration:none;width:150px;color:#fff;padding:8px;text-align:center;background-color:#409489;border:4px solid #616161;border-radius:5px;font-size:24px;transition:.3s background-color ease-in-out}.error__button:hover{background-color:rgba(133,133,133,0.911);color:#292929}

